<template lang="html">
  <div class="layout">
        <Layout>
            <Header>
              <div class="title">云一竹</div>
            </Header>
            <Content>
              <div class="content">

                <div class="brief-info">
                  <div class="left-info">
                    <img src="../../static/head.png"/>
                  </div>
                  <div class="right-info">
                    <div class="name">
                      <span>云一竹</span>
                    </div>
                    <div class="position">
                      <span>无业</span>
                    </div>
                    <div class="other-info">
                      <span>男，1993，南京</span>
                    </div>
                  </div>
                </div>

                <div class="experience-info">
                  <div class="list-title">
                    个人经历
                  </div>
                  <ul style="padding-left: 40px" class="content-list">
                    <li v-for="(item,index) in data" :key="index">
                      <span>{{item.date}}</span>
                      <span>{{item.content}}</span>
                    </li>
                  </ul>
                </div>

              </div>
            </Content>
            <Footer class="layout-footer-center">2015-2019 &copy; 云一竹</Footer>
        </Layout>
    </div>
</template>

<script>
export default {
  data() {
    return {
      data: [{
          date: '2014-2016',
          content: '江苏南亿迪纳科技股份有限公司'
        },
        {
          date: '2017-2019',
          content: '南京云创大数据科技股份有限公司'
        }
      ]
    }
  }
}
</script>

<style lang="css">
.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
    font-size: 15px;
}
.ivu-layout-content {
  background-color: #FFFFFF;
}
.title {
  color: #fff;
  font-size: 2em;
  text-align: center;
}
.content {
  overflow: hidden;
    max-width: 760px;
    margin: auto;
}
.brief-info {
  margin: 20px auto;
  display: flex;
  border-width: 1px;
  border-color: #F8F8F8;
  border-style: solid;
  box-shadow: 0px 2px 2px #B7B7B7;
  border-radius: 15px;
}
.left-info {
  width: 100px;
  height: 80px;
  margin: 10px 20px;
}
.right-info {
  margin: 10px 15px;
}
.name {
  font-size: 20px;
  font-weight: bold;
}
.left-info img {
  width: 100%;
  height: 100%;
}

.experience-info {
  border-width: 1px;
  border-color: #F8F8F8;
  border-style: solid;
  box-shadow: 0px 2px 2px #B7B7B7;
  border-radius: 15px;
  margin-bottom: 20px;
}

.list-title {
  font-size: 18px;
  margin-left: 15px;
}

.layout-footer-center{
    text-align: center;
}
</style>



